import React, { useEffect, useRef, useState } from "react";
import "./EventPoaster.css";

/**
 * í¬ìŠ¤í„° ì—…ë¡œë” (ì‘ì€ í”Œë¡œíŒ… ë¯¸ë¦¬ë³´ê¸°)
 * - ì¦‰ì‹œ í™”ë©´ ë…¸ì¶œ X
 * - "ë¯¸ë¦¬ë³´ê¸°" ë²„íŠ¼ í´ë¦­ ì‹œ ìš°í•˜ë‹¨ ì‘ì€ ì°½ìœ¼ë¡œ í‘œì‹œ
 *
 * Props:
 *  - onChange?: (file: File | null) => void
 *  - maxSizeMB?: number (ê¸°ë³¸ 5MB)
 */
export default function EventPoaster({ onChange, maxSizeMB = 5 }) {
  const fileInputRef = useRef(null);
  const [file, setFile] = useState(null);
  const [previewURL, setPreviewURL] = useState("");
  const [open, setOpen] = useState(false);

  const handleFileChange = (e) => {
    const f = e.target.files?.[0];
    if (!f) return;

    // ì´ë¯¸ì§€ íƒ€ì…/í¬ê¸° ê°„ë‹¨ ê²€ì¦
    if (!f.type.startsWith("image/")) {
      alert("ì´ë¯¸ì§€ íŒŒì¼ë§Œ ì—…ë¡œë“œí•  ìˆ˜ ìˆìŠµë‹ˆë‹¤.");
      if (fileInputRef.current) fileInputRef.current.value = "";
      setFile(null);
      onChange?.(null);
      return;
    }
    const maxBytes = maxSizeMB * 1024 * 1024;
    if (f.size > maxBytes) {
      alert(`ì´ë¯¸ì§€ í¬ê¸°ëŠ” ${maxSizeMB}MB ì´í•˜ì—¬ì•¼ í•©ë‹ˆë‹¤.`);
      if (fileInputRef.current) fileInputRef.current.value = "";
      setFile(null);
      onChange?.(null);
      return;
    }

    setFile(f);
    onChange?.(f);
  };

  // Blob URL ìƒì„±/í•´ì œ
  useEffect(() => {
    if (!file) {
      setPreviewURL("");
      return;
    }
    const url = URL.createObjectURL(file);
    setPreviewURL(url);
    return () => URL.revokeObjectURL(url);
  }, [file]);

  // ESCë¡œ ë¯¸ë¦¬ë³´ê¸° ì°½ ë‹«ê¸°
  useEffect(() => {
    if (!open) return;
    const onKeyDown = (e) => e.key === "Escape" && setOpen(false);
    window.addEventListener("keydown", onKeyDown);
    return () => window.removeEventListener("keydown", onKeyDown);
  }, [open]);

  const clearFile = () => {
    setFile(null);
    setPreviewURL("");
    if (fileInputRef.current) fileInputRef.current.value = "";
    onChange?.(null);
  };

  return (
    <div className="poster-upload">
      {/* ì—…ë¡œë“œ ë²„íŠ¼ */}
      <label className="btn poster-btn" title="ì´ë²¤íŠ¸ í¬ìŠ¤í„° ì—…ë¡œë“œ">
        í¬ìŠ¤í„° ì—…ë¡œë“œ
        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={handleFileChange}
          style={{ display: "none" }}
        />
      </label>

      {/* ë¯¸ë¦¬ë³´ê¸° / ì´ˆê¸°í™” */}
      <button
        type="button"
        className="btn poster-btn"
        onClick={() => setOpen(true)}
        disabled={!file}
        title={file ? "ë¯¸ë¦¬ë³´ê¸°" : "ì´ë¯¸ì§€ë¥¼ ë¨¼ì € ì„ íƒí•˜ì„¸ìš”"}
      >
        ë¯¸ë¦¬ë³´ê¸°
      </button>
      <button
        type="button"
        className="btn poster-btn"
        onClick={clearFile}
        disabled={!file}
        title={file ? "ì„ íƒí•œ ì´ë¯¸ì§€ ì œê±°" : "ì œê±°í•  ì´ë¯¸ì§€ê°€ ì—†ìŠµë‹ˆë‹¤"}
      >
        ì œê±°
      </button>

      {/* ì„ íƒëœ íŒŒì¼ëª…(í…ìŠ¤íŠ¸ë§Œ) */}
      {file && (
        <div className="poster-filename" title={file.name}>
          {file.name}
        </div>
      )}

      {/* ğŸ”¹ ìš°í•˜ë‹¨ ì‘ì€ ë¯¸ë¦¬ë³´ê¸° íŒ¨ë„ (ì˜¤ë²„ë ˆì´ ì—†ìŒ) */}
      {open && (
        <div
          className="poster-float"
          role="dialog"
          aria-modal="false"
          aria-label="í¬ìŠ¤í„° ë¯¸ë¦¬ë³´ê¸°"
        >
          <div className="poster-float-header">
            <strong>í¬ìŠ¤í„° ë¯¸ë¦¬ë³´ê¸°</strong>
            <button
              type="button"
              className="poster-close"
              onClick={() => setOpen(false)}
              aria-label="ë‹«ê¸°"
            />
          </div>
          <div className="poster-float-body">
            {previewURL ? (
              <img
                src={previewURL}
                alt="ì´ë²¤íŠ¸ í¬ìŠ¤í„° ë¯¸ë¦¬ë³´ê¸°"
                className="poster-preview-img"
              />
            ) : (
              <p>ì„ íƒëœ ì´ë¯¸ì§€ê°€ ì—†ìŠµë‹ˆë‹¤.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}


