import React, { useState } from "react";
import "./rewardCountStyle.css";

function RewardCount() {
  // ë¬¸ìì—´ë¡œ ê´€ë¦¬í•˜ë©´ placeholder(ë¹ˆ ê°’) ì²˜ë¦¬ì™€ ì»¨íŠ¸ë¡¤ë“œ input ìœ ì§€ê°€ ì‰¬ì›Œìš”
  const [count, setCount] = useState("");

  const handleChange = (e) => {
    const raw = e.target.value;
    if (raw === "") {
      setCount(""); // placeholder ìƒíƒœ ìœ ì§€
      return;
    }
    // ìˆ«ì ì •ê·œí™”: ìŒìˆ˜/ì†Œìˆ˜/NaN ë°©ì§€, ì •ìˆ˜ë¡œ ê³ ì •
    const n = Math.max(0, Math.floor(Number(raw)));
    setCount(String(n));
  };

  return (
    <div>
      {/* âœ… ì„œë²„ ì „ì†¡ìš© ë°±ì—…(ë²„íŠ¼ì—ì„œ ì½ìŒ) */}
      <input
        type="hidden"
        id="event-reward-count"
        value={count}
        readOnly
      />

      <input
        type="number"
        className={`reward-input ${count === "" ? "is-placeholder" : ""}`}
        placeholder="ë³´ìƒ ìˆ˜ëŸ‰"
        value={count}
        onChange={handleChange}
        min={0}
        step={1}
        inputMode="numeric"
        pattern="[0-9]*"
      />
    </div>
  );
}

export default RewardCount;
